@import url(https://fonts.googleapis.com/css2?family=Montserrat&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: "Montserrat", sans-serif;
}

.crypto-app {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 64px;
}

.crypto-search {
  margin-bottom: 64px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-content: center;
}

.crypto-search-currency {
  margin-bottom: 32px;
  text-align: center;
}

.crypto-search-input {
  padding-left: 16px;
  width: 300px;
  height: 50px;
  border-radius: 4px;
  border: none;
  background-image: linear-gradient(
    -225deg,
    #0a0c0a 0%,
    #120324 48%,
    #000000 100%
  );

  color: #e2e2e2;
}

.crypto-search-input::-webkit-input-placeholder {
  color: #e2e2e2;
}

.crypto-search-input:-ms-input-placeholder {
  color: #e2e2e2;
}

.crypto-search-input::placeholder {
  color: #e2e2e2;
}

.paginationBttns {
  width: 80%;
  height: 40px;
  list-style: none;
  display: flex;
  justify-content: center;
  margin-top: 30px;
  /* background-color: #b3e3ff; */
}

.paginationBttns a {
  padding: 10px;
  margin: 8px;
  border-radius: 5px;
  border: 1px solid #2b2eff;
  color: #2b2eff;
  cursor: pointer;
}

.paginationBttns a:hover {
  color: white;
  background-color: #2b2eff;
}

.paginationActive a {
  color: white;
  background-color: #2b2eff;
}

.paginationDisabled a {
  color: grey;
  background-color: grey;
}

.crypto-container {
  display: flex;
  justify-content: center;
}
.crypto-row {
  display: flex;
  flex-direction: row;
  justify-items: start;
  align-items: center;
  height: 80px;
  border-bottom: 1px solid #ddd;
  width: 900px;
}
.crypto {
  display: flex;
  align-items: center;
  padding-right: 24px;
  min-width: 300px;
}
.crypto img {
  height: 30px;
  width: 30px;
  margin-right: 10px;
}
.crypto h1 {
  font-size: 16px;
  width: 150px;
}
.crypto-symbol {
  text-transform: uppercase;
}
.crypto-data {
  display: flex;
  text-align: center;
  justify-content: space-between;
  width: 100%;
}
.crypto-price {
  width: 110px;
}
.crypto-volume {
  width: 155px;
}
.crypto-marketcap {
  width: 230px;
}
.crypto-percent {
  width: 100px;
}
.red {
  color: red;
}
.green {
  color: green;
}

