.crypto-container {
  display: flex;
  justify-content: center;
}
.crypto-row {
  display: flex;
  flex-direction: row;
  justify-items: start;
  align-items: center;
  height: 80px;
  border-bottom: 1px solid #ddd;
  width: 900px;
}
.crypto {
  display: flex;
  align-items: center;
  padding-right: 24px;
  min-width: 300px;
}
.crypto img {
  height: 30px;
  width: 30px;
  margin-right: 10px;
}
.crypto h1 {
  font-size: 16px;
  width: 150px;
}
.crypto-symbol {
  text-transform: uppercase;
}
.crypto-data {
  display: flex;
  text-align: center;
  justify-content: space-between;
  width: 100%;
}
.crypto-price {
  width: 110px;
}
.crypto-volume {
  width: 155px;
}
.crypto-marketcap {
  width: 230px;
}
.crypto-percent {
  width: 100px;
}
.red {
  color: red;
}
.green {
  color: green;
}
